import React, { Component } from "react";
import { Button, Form, Container, Header } from 'semantic-ui-react';
import { Link } from 'gatsby';
import evalFormDataService from "./evalform.service";
import "../css/evalForm.css";

export default class addEvalForm extends Component {
    constructor(props) {
        super(props);
        this.onChangeDisciplinary = this.onChangeDisciplinary.bind(this);
        this.onChangeIntellectual = this.onChangeIntellectual.bind(this);
        this.onChangeInterpersonal = this.onChangeInterpersonal.bind(this);
        this.onChangeGlobal = this.onChangeGlobal.bind(this);
        this.onChangePersonal = this.onChangePersonal.bind(this);
        this.onChangeContributions = this.onChangeContributions.bind(this);
        this.onChangePercent = this.onChangePercent.bind(this);
        this.onChangeImprovement = this.onChangeImprovement.bind(this);
        this.onChangeStudent = this.onChangeStudent.bind(this);
        this.saveEvalForm = this.saveEvalForm.bind(this);
        this.newEvalForm = this.newEvalForm.bind(this);

        this.state = {
            DisciplinaryKnowledge_value: "",
            IntellectualCreativeSkills_value: "",
            InterpersonalSkills_value: "",
            GlobalCitizenship_value: "",
            PersonalMastery_value: "",
            Contributions_value: "",
            Contributionpercent_value: "",
            Improvement_value: "", 
            StudentReciever_id: "",

            submitted: false
        };
    }

    onChangeDisciplinary(e) {
        this.setState({
            DisciplinaryKnowledge_value: e.target.value
        });
    }

    onChangeIntellectual(e) {
        this.setState({
            IntellectualCreativeSkills_value: e.target.value
        });
    }

    onChangeInterpersonal(e) {
        this.setState({
            InterpersonalSkills_value: e.target.value
        });
    }

    onChangeGlobal(e) {
        this.setState({
            GlobalCitizenship_value: e.target.value
        });
    }

    onChangePersonal(e) {
        this.setState({
            PersonalMastery_value: e.target.value
        });
    }

    onChangeContributions(e) {
        this.setState({
            Contributions_value: e.target.value
        });
    }

    onChangePercent(e) {
        this.setState({
            Contributionpercent_value: e.target.value
        })
    }

    onChangeImprovement(e) {
        this.setState({
            Improvement_value: e.target.value
        });
    }

    onChangeStudent(e) {
        this.setState({
            StudentReciever_id: e.target.value
        });
    }

    saveEvalForm() {
        var data = {
            DisciplinaryKnowledge_value: this.state.DisciplinaryKnowledge_value,
            IntellectualCreativeSkills_value: this.state.IntellectualCreativeSkills_value,
            InterpersonalSkills_value: this.state.InterpersonalSkills_value,
            GlobalCitizenship_value: this.state.GlobalCitizenship_value,
            PersonalMastery_value: this.state.PersonalMastery_value,
            Contributions_value: this.state.Contributions_value,
            Contributionpercent_value: this.state.Contributionpercent_value,
            Improvement_value: this.state.Improvement_value,
            StudentReciever_id: this.state.StudentReciever_id
        };
        console.log(data);

        evalFormDataService.create(data)
            .then(response => {
                this.setState({
                    DisciplinaryKnowledge_value: response.data.DisciplinaryKnowledge_value,
                    IntellectualCreativeSkills_value: response.data.IntellectualCreativeSkills_value,
                    InterpersonalSkills_value: response.data.InterpersonalSkills_value,
                    GlobalCitizenship_value: response.data.GlobalCitizenship_value,
                    PersonalMastery_value: response.data.PersonalMastery_value,
                    Contributions_value: response.data.Contributions_value,
                    Contributionpercent_value: response.data.Contributionpercent_value,
                    Improvement_value: response.data.Improvement_value,
                    StudentReciever_id: response.data.StudentReciever_id,

                    submitted: true
                });
                console.log(response);
            })
            .catch(e => {
                console.log(e);
            });
    }

    newEvalForm() {
        this.setState({
            DisciplinaryKnowledge_value: "",
            IntellectualCreativeSkills_value: "",
            InterpersonalSkills_value: "",
            GlobalCitizenship_value: "",
            PersonalMastery_value: "",
            Contributions_value: "",
            Contributionpercent_value: "",
            Improvement_value: "",
            StudentReciever_id: null,

            submitted: false
        });
    }

    render() {
        return (
            <div className="submit-form">
                {this.state.submitted ? (
                    <div>
                        <h4>You submitted successfully!</h4>
                        <button className="btn btn-success" onClick={this.newEvalForm}>
                            Submit Another Form
                        </button>
                        <Button variant="light"><Link to="/menu">Back to Menu</Link></Button>
                    </div>
                ) : (
                        <Container fluid className="container">
                            <Header as="h2">Peer Evaluation For: </Header>
                            {/* Implement drop down box with names. */}
                            <select name="StudentReciever_id" id="StudentReciever_id" value={this.state.StudentReciever_id} onChange={this.onChangeStudent}>
                                <option value="default" selected="true">Choose one name.</option>
                                <option>Jacob Mims</option>
                                <option>Pavan Sastri</option>
                                <option>Kirk Graham</option>
                                <option>Jacquelyn</option>
                                <option>Allie Ahwee-Marrah</option>
                                <option>Derek Stoughton</option>
                                <option>Natalie Calizon</option>
                                <option>Vignesh Sinha</option>
                            </select>
                            <div></div><br />

                            <Form className="form">

                                <Form.Field>
                                    <bold>1. What grade out of 100 do you think this individual should receive based on their efforts with the group project?</bold><br />
                                    <label class="formlabels">Disciplinary + Multidisciplinary Knowledge  </label>
                                    <input type="text" id="DisciplinaryKnowledge_value" name="DisciplinaryKnowledge_value" size="3" onChange={this.onChangeDisciplinary} value={this.state.DisciplinaryKnowledge_value}></input><br />
                                    <label class="formlabels">Intellectual + Creative Skills  </label>
                                    <input type="text" id="IntellectualCreateiveSkills_value" name="IntellectualCreateiveSkills_value" size="3" onChange={this.onChangeIntellectual} value={this.state.IntellectualCreativeSkills_value}></input><br />
                                    <label class="formlabels">Interpersonal Skills  </label>
                                    <input type="text" id="InterpersonalSkills_value" name="InterpersonalSkills_value" size="3" onChange={this.onChangeInterpersonal} value={this.state.InterpersonalSkills_value}></input><br />
                                    <label class="formlabels">Global Citizenship  </label>
                                    <input type="text" id="GlobalCitizenship_value" name="GlobalCitizenship_value" size="3" onChange={this.onChangeGlobal} value={this.state.GlobalCitizenship_value}></input><br />
                                    <label class="formlabels">Personal Mastery  </label>
                                    <input type="text" id="PersonalMastery_value" name="PersonalMastery_value" size="3" onChange={this.onChangePersonal} value={this.state.PersonalMastery_value}></input>
                                    <div></div><br />
                                </Form.Field>

                                <Form.Field>
                                    <bold>2. Please describe the contributions of this team member below.</bold><br />
                                    <textarea id="Contributions_value" name="Contributions_value" rows="10" cols="50" onChange={this.onChangeContributions} value={this.state.Contributions_value}></textarea>
                                    <div></div><br />
                                </Form.Field>

                                <Form.Field>
                                    <bold>3. Please select the option that best aligns to your analysis of the member's teamwork and communication skills.</bold>
                                    <div class="radio">
                                        <input type="radio" id="choice1" name="Teamcommunicationskill_value" value={this.state.Contributions_value} onChange={this.onChangeContributions} value={this.state.Contributions_value}></input>
                                        <label for="choice1">Terrible</label>
                                        <input type="radio" id="choice2" name="Teamcommunicationskill_value" value={this.state.Contributions_value} onChange={this.onChangeContributions} value={this.state.Contributions_value}></input>
                                        <label for="choice2">Sub-Par</label>
                                        <input type="radio" id="choice3" name="Teamcommunicationskill_value" value={this.state.Contributions_value} onChange={this.onChangeContributions} value={this.state.Contributions_value}></input>
                                        <label for="choice3">Average</label>
                                        <input type="radio" id="choice4" name="Teamcommunicationskill_value" value={this.state.Contributions_value} onChange={this.onChangeContributions} value={this.state.Contributions_value}></input>
                                        <label for="choice4">Good</label>
                                        <input type="radio" id="choice5" name="Teamcommunicationskill_value" value={this.state.Contributions_value} onChange={this.onChangeContributions} value={this.state.Contributions_value}></input>
                                        <label for="choice5">Excellent</label>
                                    </div>
                                    <div></div><br />
                                </Form.Field>

                                <Form.Field>
                                    <bold>4. What % contribution did this member have towards the project?</bold><br />
                                    <input type="text" id="Contributionpercent_value" name="Contributionpercent_value" size="3" onChange={this.onChangePercent} value={this.state.Contributionpercent_value}></input>%
                    <div></div><br />
                                </Form.Field>

                                <Form.Field>
                                    <bold>5. What could this member do to improve?</bold><br />
                                    <textarea id="Improvement_value" name="Improvement_value" rows="5" cols="50" onChange={this.onChangeImprovement} value={this.state.Improvement_value}></textarea>
                                    <div></div><br />
                                </Form.Field>

                                <Button color="blue" type='submit' onClick={this.saveEvalForm}>Submit</Button>
                            </Form>
                        </Container>
                )}
            </div>
        );
    }
}