import http from "./http-common";

class evalFormDataService {
    getAll() {
        return http.get("/evalforms/");
    }

    get(id) {
        return http.get(`/evalforms/${id}`);
    }

    create(data) {
        return http.post("/evalforms", data);
    }

    update(id, data) {
        return http.put(`/evalforms/${id}`, data);
    }

    delete(id) {
        return http.delete(`/evalforms/${id}`);
    }

    deleteAll() {
        return http.delete('/evalforms/');
    }
}

export default new evalFormDataService();